.alignfull {
  width: 100%;
  margin: 0 auto 0.5em auto;
  float: none;
  padding-top: 0.5em;
  padding-right: 0;
  padding-left: 0;
}
.alignleft figcaption,
.alignright figcaption,
.alignwide figcaption,
.alignfull figcaption {
  text-align: center;
}
@media screen and (min-width: 640px) {
  .alignleft,
.alignright,
.alignwide,
.alignfull {
    margin: 0 0 0.5em 0;
  }
}

@media screen and (min-width: 640px) {
  .alignleft {
    float: left;
    padding-right: 0.75em;
  }
}

@media screen and (min-width: 640px) {
  .alignright {
    float: right;
    padding-left: 0.75em;
  }
}

.alignwide, .alignfull {
  margin: 0 auto 1em auto;
}
.alignwide div iframe, .alignfull div iframe {
  width: 100%;
  height: 75%;
}

#respond {
  margin: 0.75em auto 0;
  width: 85%;
}

#commentform p {
  margin-bottom: 0.25em;
}
#commentform p label {
  display: block;
  font-size: 0.75em;
  font-weight: bold;
  text-transform: uppercase;
  font-family: sans-serif;
}
#commentform p input[type=text] {
  width: 50%;
}

.comment-form-comment textarea {
  display: block;
  width: 100%;
  resize: vertical;
}

.comment-form-cookies-consent label {
  display: inline !important;
}

.form-submit #submit {
  font-family: inherit;
  font-size: inherit;
  color: #444;
  display: block;
  margin: 0 auto;
  text-align: center;
  padding: 0.5em 1em;
  border-radius: 5px;
}

#cancel-comment-reply-link {
  font-size: 0.75em;
  font-weight: bold;
  text-transform: uppercase;
  font-family: sans-serif;
}
#cancel-comment-reply-link::before {
  content: "(";
}
#cancel-comment-reply-link::after {
  content: ")";
}

.comments {
  background: rgba(240, 234, 210, 0.07);
  padding: 1em;
  border-radius: 7px;
  font-weight: 400;
  font-size: 1rem;
  width: 100%;
  display: block;
}
.comments--title {
  text-align: center;
  margin: 0 auto 0.5em;
}

.required-field-message {
  display: none;
}

.comment {
  width: auto;
  padding: 0 1em;
  margin-bottom: 1.5em;
}
.comment--avatar {
  float: left;
  padding-right: 0.75em;
}
.comment--content > * {
  margin-bottom: 0.25em;
}
.comment .reply {
  font-size: 0.75em;
  font-weight: bold;
  text-transform: uppercase;
  font-family: sans-serif;
}
.comment .timecode {
  font-size: 0.75em;
  font-weight: bold;
  text-transform: uppercase;
  font-family: sans-serif;
}
.comment .timecode a {
  text-decoration: none;
}
.comment .depth-2, .comment .depth-3, .comment .depth-4, .comment .depth-5 {
  margin-top: 1em;
  border-left: 3px solid #444;
}