// Breakpoints
$mobile-sm: 540px;
$mobile-md: 640px;
$mobile-lg: 840px;
$tablet-sm: 1040px;
$tablet-lg: 1250px;
$desktop: 1400px;
$expanded: 1540px;

// Image sizes
$img_lg_max: 220px;
$img_lg_min: 170px;
$img_md_max: 170px;
$img_md_min: 140px;
$img_sm: 100px;

// Colors
$shadow: #151223;
$orange: #F46B4B;
$pearl: rgba(240, 234, 210, .07);
$blk1: rgb(49, 49, 49);
$blk2: #646464;
$blk3: #444;

// Noise mask
$noise: url('data:image/png;base64,');

// Mixins
@mixin card {
  background: $pearl;
  padding: 1em;
  border-radius: 7px;
  font-weight: 400;
  font-size: 1rem;
}

@mixin small-text {
  font-size: .75em;
  font-weight: bold;
  text-transform: uppercase;
  font-family: sans-serif;
}

@mixin tzimmes($weight: 700) {
  font-family: tzimmes, san-serif;
  line-height: 1;
  font-weight: $weight;
}